
*{
  margin: 0 auto;
  padding: 0;
}
#app {
font-family: 'Avenir', Helvetica, Arial, sans-serif;
-webkit-font-smoothing: antialiased;
-moz-osx-font-smoothing: grayscale;
text-align: center;
color: #2c3e50;
margin: 0 auto;
padding: 0;
width: 400px;
}
#app img{
  max-width: 150px;
}
h1{
  font-size: 60px;
}
#todolist{
  background: white;
}
.textPut{
  position: relative;
  width: 100%;
  font-size: 30px;
}
.textPut button{
  width: 36px;
  height: 46px;
  position: absolute;
  background: darkseagreen;
  left: 10px;
  top: 2px;
  border-radius: 10px;
  border-color: ghostwhite;
  border-style: solid;
  border-width: 5px;
}
.textPut input{
  width: 330px;
  height: 40px;
  padding: 5px 5px 5px 50px;
  font-size: 30px;
  border: 0;
}
ul{
  width: 300px;
  margin: 0;
  margin-left: 30px;
  text-align: center;
  font-family: Microsoft "Microsoft YaHei";
  font-size: 22px;
  list-style: decimal;
}
ul li{
  width: 300px;
  padding-right: 70px;
  position: relative;
  border-bottom:1px solid grey;
}
ul li label{
  width: 240px;
  padding: 0 30px 0 40px;
  word-break: break-all;
}
ul li .editinp{
  border:0;
  font-size: 22px;
  width: 220px;
  padding: 0 30px 0 10px;
  margin-left: 30px;
  color: #2c3e50;
  float: left;
}
ul li span.editList{
  position: absolute;
  left: 0;
  top: 0;
}
ul li span.compList{
  position: absolute;
  right: 0;
  top: 0;
}
ul li span.deleList{
  position: absolute;
  right: 40px;
  top: 0;
}
.editList,.compList,.deleList{
  font-size: 15px;
  color: green;
  cursor: pointer;
}
.finished label{
  text-decoration: line-through;
}
footer{
  position: relative;
  height: 30px;
  text-align: center;
  font-size: 15px;
}
footer:before{
  content:'';
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: 20px;
  overflow: hidden;
  -webkit-box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2), 0 8px 0 -3px #f6f6f6, 0 9px 1px -3px rgba(0, 0, 0, 0.2), 0 16px 0 -6px #f6f6f6, 0 17px 2px -6px rgba(0, 0, 0, 0.2);
          box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2), 0 8px 0 -3px #f6f6f6, 0 9px 1px -3px rgba(0, 0, 0, 0.2), 0 16px 0 -6px #f6f6f6, 0 17px 2px -6px rgba(0, 0, 0, 0.2);
}
footer #todoCount,.all,.already,.able,.clearfin{
  top:5px;
  position: absolute;
}
footer div.butkeep{
  padding: 0 2px;
  border-radius: 4px;
  border: 1px solid lightgrey;
}
.all{
  left:250px;
  padding: 0 2px;
  border: 1px solid white;
}
.already{
  left:110px;
  padding: 0 2px;
  border: 1px solid white;
}
.able{
  padding: 0 2px;
  left:180px;
  border: 1px solid white;
}
a.clearfin:link,a.clearfin:visited,a.clearfin:active{
  right: 0;
  text-decoration: none;
  color: #2c3e50;
}
